import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

export default new Router({
	routes: [{
			path: '/',
			redirect: '/index'
		},
		{
			path: '/login',
			name: 'login',
			meta: {
				title: '登录'
			},
			component: resolve => require(['@/views/login'], resolve)
		},
		{
			path: '/fill',
			name: 'fill',
			meta: {
				title: '填写资料',
				requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
			},
			component: resolve => require(['@/views/fill'], resolve)
		},
		{
			path: '/index',
			name: 'index',
			meta: {
				title: '我的培训',
				requireAuth: true
			},
			component: resolve => require(['@/views/index'], resolve)
		},
		{
			path: '/personal',
			name: 'personal',
			meta: {
				title: '个人中心',
				requireAuth: true
			},
			component: resolve => require(['@/views/personal'], resolve)
		},
		{
			path: '/detail',
			name: 'detail',
			meta: {
				title: '培训详情',
				requireAuth: true
			},
			component: resolve => require(['@/views/detail'], resolve),
			redirect: '/guide',
			children: [{
				path: "/guide",
				name: "guide",
				meta: {
					title: '培训指引',
					requireAuth: true
				},
				component: resolve => require(['@/views/detail/guide'], resolve)
			}, {
				path: "/examlist",
				name: "examlist",
				meta: {
					title: '考试测验',
					requireAuth: true
				},
				component: resolve => require(['@/views/detail/examlist'], resolve)
			}, {
				path: "/questionlist",
				name: "questionlist",
				meta: {
					title: '问卷调查',
					requireAuth: true
				},
				component: resolve => require(['@/views/detail/questionlist'], resolve)
			}, {
				path: "/scorelist",
				name: "scorelist",
				meta: {
					title: '成绩查询',
					requireAuth: true
				},
				component: resolve => require(['@/views/detail/scorelist'], resolve)
			}]
		},
		{
			path: "/question",
			name: "question",
			meta: {
				title: '评估卷',
				requireAuth: true
			},
			component: resolve => require(['@/views/detail/question'], resolve)
		},
		{
			path: "/exam",
			name: "exam",
			meta: {
				title: '测验卷',
				requireAuth: true
			},
			component: resolve => require(['@/views/detail/exam'], resolve)
		},
		{
			path: "/score",
			name: "score",
			meta: {
				title: '成绩详情',
				requireAuth: true
			},
			component: resolve => require(['@/views/detail/score'], resolve)
		},
		{
			path: "/situation",
			name: "situation",
			meta: {
				title: '答案解析',
				requireAuth: true
			},
			component: resolve => require(['@/views/detail/situation'], resolve)
		}
	]
})


