const ls = window.localStorage
const ss = window.sessionStorage

let utils = {
	/**
	 * 
	 * @param {String} fmt 
	 * @param {时间} date 
	 */
	dateFtt(fmt, date) { //dateFtt('yyyy-MM-dd hh:mm:ss',a)
		let time
		if(!date) return ""
		if(typeof date === 'string') {
			time = new Date(date.replace(/-/g, '/').replace(/T|Z/g, ' ').trim())
		} else if(typeof date === 'object') {
			time = new Date(date)
		}
		let o = {
			"M+": time.getMonth() + 1, //月份
			"d+": time.getDate(), //日
			"h+": time.getHours(), //小时
			"m+": time.getMinutes(), //分
			"s+": time.getSeconds(), //秒
			"q+": Math.floor((time.getMonth() + 3) / 3), //季度
			"S": time.getMilliseconds() //毫秒
		}
		if(/(y+)/.test(fmt))
			fmt = fmt.replace(RegExp.$1, (time.getFullYear() + "").substr(4 - RegExp.$1.length))
		for(var k in o)
			if(new RegExp("(" + k + ")").test(fmt))
				fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)))
		return fmt
	},
	// 转换为日期格式
	toDate(str) {
		let arr = str.replace(/(-|\s|\:|\.)/g, ',').split(',')
		let y = arr[0] || null
		let m = arr[1] || null
		m = m ? m -1 : m
		let d = arr[2] || null
		let h = arr[3] || null
		let min = arr[4] || null
		let s = arr[5] || null
		let ss = arr[6] || null
		let newDate = new Date(y, m, d, h, min, s, ss)
		return newDate
	},
	/*
	 * 获取本地存储
	 */
	getItem(key) {
		try {
			let o = JSON.parse(ls[key])
			if(o.expires) {
				if(o.expires < Date.now()) {
					ls.removeItem(key)
					return null
				} else {
					return o.val
				}
			} else {
				return o
			}
		} catch(e) {
			return ls[key]
		}
	},
	/*
	 * 设置本地存储
	 * @param {[type]} key   [键名]
	 * @param {[type]} val [键值]
	 * @param {[type]} days  [保存的时间（天）]
	 */
	setItem(key, val, days) {
		// 设置过期原则
		if(!val) {
			ls.removeItem(key)
		} else {
			var exp = new Date()
			if(days) {
				ls[key] = JSON.stringify({
					val,
					expires: exp.getTime() + days * 24 * 60 * 60 * 1000
				})
			} else {
				ls[key] = JSON.stringify(val)
			}
		}
	},
	/*
	 * 清除本地存储
	 */
	clear() {
		ls.clear()
	},
	/*
	 * 清除本地存储
	 */
	removeItem(key) {
		ls.removeItem(key)
	}
}

export default utils


