import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let store = new Vuex.Store({
	state: {
		flag: '',
		info: {}
	},
	getters: {
		flag: state => state.flag,
		info: state => state.info
	},
	mutations: {
		setFlag(state, flag) {
			state.flag = flag
		},
		setInfo(state, info) {
			state.info = info
		}
	}
})

export default store


