import Vue from 'vue'
import qs from 'qs'
import axios from "axios"
import { Notify } from 'vant'
let BASE_URL = ''

if(process.env.NODE_ENV === 'development') {
	BASE_URL = "http://172.16.10.199:8081/emp" // 本地地址
} else {
	BASE_URL = "http://10.53.1.190:8080/emp" //上线的服务器地址
}
BASE_URL = CONFIG.API || BASE_URL

const http = axios.create({
	baseURL: BASE_URL,
	timeout: 60000
})

// request 拦截器
http.interceptors.request.use(config => {
	//config.headers['token'] = Vue.cookie.get('token')
	return config
}, error => {
	return Promise.reject(error)
})

// response 拦截器
http.interceptors.response.use(response => {
	const res = response.data
	console.log(res)
	if(response.status === 200 && (res.code === 0 || res.rCode === 0)) {
		return res
	} else {
		Notify({
			type: 'danger',
			message: res.message || '系统繁忙！'
		})
		return res
	}
}, error => {
	Notify({
		type: 'danger',
		message: '系统繁忙！'
	})
	return Promise.reject(error)
})

export default {
	get(url, params = {}) {
		return http.get(url, {
			params: Object.assign(params, {
				r: new Date().getTime()
			})
		})
	},
	post(url, params = {}) {
		return http({
			method: 'post',
			url: url,
			data: qs.stringify(params),
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
			}
		})
	}
}


